.statistics-modal {
    .statistics-wrapper {
        padding-bottom: 20px;
        &:first-child {
            border-bottom: 1px solid #000;
        }
        &:last-child {
            padding-top: 20px;
        }
        .title {
            font-size: 16px;
            font-weight: bolder;
            margin-bottom: 10px;
        }
        .statistics-content {
            overflow: hidden;
            padding: 0 20px;
            li {
                width: 60%;
                float: left;
                font-size: 16px;
                .category {
                    display: inline-block;
                    width: 100px;
                    
                }
                &:first-child .category{
                    color: rgb(4, 193, 253);
                }
                &:nth-child(2) .category{
                    color: rgb(255, 63, 25);
                }
                &:nth-child(3) .category{
                    color: rgb(105, 231, 28);
                }
                &:last-child .category{
                    color: indigo;
                }
                &:nth-child(even) {
                    width: 40%;
                }
            }
        }
    }
}